"use client";

import Link from "next/link";
import { useRouter } from "next/navigation";
import { useEffect, useMemo, useState } from "react";

import { StatsDashboard } from "@/components/StatsDashboard";
import { StatsApiResponse } from "@/lib/types";

interface ApiError {
  error?: string;
}

const formatTime = (iso: string): string =>
  new Date(iso).toLocaleString("en-US", {
    month: "short",
    day: "numeric",
    hour: "numeric",
    minute: "2-digit",
  });

export default function DashboardPage() {
  const router = useRouter();
  const [token, setToken] = useState<string | null>(null);
  const [stats, setStats] = useState<StatsApiResponse | null>(null);
  const [loading, setLoading] = useState(true);
  const [syncing, setSyncing] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const gatewayConfig = useMemo(() => {
    if (typeof window === "undefined") {
      return null;
    }

    const gatewayUrl = localStorage.getItem("clawboard.gatewayUrl");
    const apiKey = localStorage.getItem("clawboard.apiKey");

    if (!gatewayUrl && !apiKey) {
      return null;
    }

    return { gatewayUrl, apiKey };
  }, []);

  const loadStats = async (activeToken: string) => {
    const response = await fetch(`/api/stats/${encodeURIComponent(activeToken)}`, { cache: "no-store" });
    if (!response.ok) {
      const body = (await response.json()) as ApiError;
      throw new Error(body.error ?? "Unable to load stats");
    }

    const data = (await response.json()) as StatsApiResponse;
    setStats(data);
  };

  useEffect(() => {
    const existingToken = localStorage.getItem("clawboard.token");
    if (!existingToken) {
      setLoading(false);
      return;
    }

    setToken(existingToken);
    loadStats(existingToken)
      .catch((loadError: unknown) => {
        const message = loadError instanceof Error ? loadError.message : "Unable to load dashboard";
        setError(message);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  const sync = async () => {
    if (!token) {
      setError("No active local session. Reconnect from the home page.");
      return;
    }

    setError(null);
    setSyncing(true);

    try {
      const response = await fetch("/api/sync", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          anonymousToken: token,
          ...(gatewayConfig?.gatewayUrl ? { gatewayUrl: gatewayConfig.gatewayUrl } : {}),
          ...(gatewayConfig?.apiKey ? { apiKey: gatewayConfig.apiKey } : {}),
        }),
      });

      if (!response.ok) {
        const body = (await response.json()) as ApiError;
        throw new Error(body.error ?? "Sync failed");
      }

      await loadStats(token);
    } catch (syncError: unknown) {
      const message = syncError instanceof Error ? syncError.message : "Sync failed";
      setError(message);
    } finally {
      setSyncing(false);
    }
  };

  const toggleShare = async () => {
    if (!token || !stats) {
      return;
    }

    const nextShareValue = !stats.shareEnabled;

    setStats({
      ...stats,
      shareEnabled: nextShareValue,
    });

    const response = await fetch("/api/share", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        anonymousToken: token,
        shareEnabled: nextShareValue,
      }),
    });

    if (!response.ok) {
      setStats({
        ...stats,
        shareEnabled: !nextShareValue,
      });
      const body = (await response.json()) as ApiError;
      setError(body.error ?? "Unable to update sharing preference");
    }
  };

  const resetLocalSession = () => {
    localStorage.removeItem("clawboard.token");
    localStorage.removeItem("clawboard.gatewayUrl");
    localStorage.removeItem("clawboard.apiKey");
    router.push("/");
  };

  const noSession = !loading && !token;

  return (
    <main className="mx-auto min-h-screen w-full max-w-6xl px-5 py-10 md:px-8 md:py-14">
      <header className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
        <div>
          <p className="text-xs uppercase tracking-[0.2em] text-[color:var(--text-muted)]">Personal Dashboard</p>
          <h1 className="mt-1 text-2xl font-semibold">Your OpenClaw usage snapshot</h1>
        </div>
        <div className="flex w-full flex-wrap items-center gap-2 text-sm md:w-auto">
          <Link
            href="/leaderboard"
            className="rounded-xl border border-[color:var(--border)] px-3 py-2 text-[color:var(--text-muted)] transition hover:text-white focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-emerald-400"
          >
            View Leaderboard
          </Link>
          <button
            type="button"
            onClick={resetLocalSession}
            disabled={!token}
            className="rounded-xl border border-[color:var(--border)] px-3 py-2 text-[color:var(--text-muted)] transition hover:text-white focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-emerald-400 disabled:cursor-not-allowed disabled:opacity-50"
          >
            Disconnect
          </button>
        </div>
      </header>

      {noSession ? (
        <section className="mt-6 rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-6">
          <p className="text-sm text-[color:var(--text-muted)]">No local session is active on this device.</p>
          <p className="mt-2 text-sm text-[color:var(--text-muted)]">
            Reconnect from the home page to resume sync and sharing.
          </p>
          <Link
            href="/"
            className="mt-4 inline-flex rounded-xl bg-emerald-400 px-4 py-2 text-sm font-semibold text-black transition hover:bg-emerald-300 focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-emerald-400"
          >
            Go Home
          </Link>
        </section>
      ) : (
        <>
          <section className="mt-6 rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-4 text-sm">
            <div className="flex flex-wrap items-center justify-between gap-3">
              <p className="text-[color:var(--text-muted)]">
                Last updated: {stats ? formatTime(stats.updatedAt) : "Not synced yet"}
              </p>
              <div className="flex items-center gap-2">
                <button
                  type="button"
                  onClick={sync}
                  disabled={syncing || !token}
                  className="rounded-lg bg-emerald-400 px-3 py-2 text-sm font-semibold text-black transition hover:bg-emerald-300 focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-emerald-400 disabled:cursor-not-allowed disabled:opacity-75"
                >
                  {syncing ? "Syncing..." : "Sync Now"}
                </button>

                <button
                  type="button"
                  onClick={toggleShare}
                  disabled={!stats}
                  className="rounded-lg border border-[color:var(--border)] px-3 py-2 text-[color:var(--text-muted)] transition hover:text-white focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-emerald-400 disabled:opacity-60"
                >
                  {stats?.shareEnabled ? "Sharing On" : "Share Anonymously"}
                </button>
              </div>
            </div>
          </section>

          {error ? (
            <p className="mt-4 rounded-lg bg-rose-900/35 px-3 py-2 text-sm text-rose-200">{error}</p>
          ) : null}

          <section className="mt-6">
            {loading ? (
              <div className="rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-8 text-sm text-[color:var(--text-muted)]">
                Loading dashboard...
              </div>
            ) : stats ? (
              <StatsDashboard stats={stats} />
            ) : (
              <div className="rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-8 text-sm text-[color:var(--text-muted)]">
                No stats available yet. Sync once to populate your dashboard.
              </div>
            )}
          </section>
        </>
      )}
    </main>
  );
}
