interface StatsCardProps {
  label: string;
  value: string;
  caption?: string;
}

export function StatsCard({ label, value, caption }: StatsCardProps) {
  return (
    <article className="rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-4">
      <p className="text-xs uppercase tracking-[0.2em] text-[color:var(--text-muted)]">{label}</p>
      <p className="mt-2 text-2xl font-semibold leading-none">{value}</p>
      {caption ? <p className="mt-1 text-sm text-[color:var(--text-muted)]">{caption}</p> : null}
    </article>
  );
}
