import { ActivityHeatmap } from "@/components/ActivityHeatmap";
import { StatsCard } from "@/components/StatsCard";
import { StatsApiResponse } from "@/lib/types";

interface StatsDashboardProps {
  stats: StatsApiResponse;
}

const formatNumber = (value: number): string => Intl.NumberFormat("en-US").format(value);
const formatMoney = (value: number): string =>
  Intl.NumberFormat("en-US", { style: "currency", currency: "USD", maximumFractionDigits: 2 }).format(value);

export function StatsDashboard({ stats }: StatsDashboardProps) {
  return (
    <div className="space-y-6">
      <section className="grid gap-4 sm:grid-cols-2 xl:grid-cols-4">
        <StatsCard label="Total Tokens" value={formatNumber(stats.totalTokens)} caption="All time" />
        <StatsCard label="Total Cost" value={formatMoney(stats.totalCost)} caption="All time" />
        <StatsCard label="Messages" value={formatNumber(stats.messageCount)} caption="All time" />
        <StatsCard label="Sessions" value={formatNumber(stats.sessionCount)} caption="All time" />
      </section>

      <section className="grid gap-4 lg:grid-cols-2">
        <article className="rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-4">
          <h3 className="text-sm font-medium text-[color:var(--text-muted)]">Top Models</h3>
          <ul className="mt-3 space-y-2">
            {stats.topModels.map((model) => (
              <li key={model.model} className="flex items-center justify-between gap-3 text-sm">
                <span>{model.model}</span>
                <span className="font-[family-name:var(--font-mono)] text-[color:var(--text-muted)]">
                  {formatNumber(model.tokens)}
                </span>
              </li>
            ))}
          </ul>
        </article>

        <article className="rounded-2xl border border-[color:var(--border)] bg-[color:var(--bg-secondary)] p-4">
          <h3 className="text-sm font-medium text-[color:var(--text-muted)]">Top Tools</h3>
          <ul className="mt-3 space-y-2">
            {stats.topTools.map((tool) => (
              <li key={tool.tool} className="flex items-center justify-between gap-3 text-sm">
                <span>{tool.tool}</span>
                <span className="font-[family-name:var(--font-mono)] text-[color:var(--text-muted)]">
                  {formatNumber(tool.count)}
                </span>
              </li>
            ))}
          </ul>
        </article>
      </section>

      <ActivityHeatmap activity={stats.activity} />
    </div>
  );
}
